import React from "react"
import { Contact, ContactType } from "."
import { ImageDataLike } from "gatsby-plugin-image"

interface ProjectTeamProps {
  title: string
  contacts: ContactType[]
  defaultImage: ImageDataLike,
  status: string
}

export const ProjectTeam: React.FC<ProjectTeamProps> = ({
  title,
  contacts,
  defaultImage,
  status
}) => {
  {/* Kevin */ }
  if (status == "ongoing" || status == "completed")
  {
    return (
      <section className="m-responsive"> 
        <h3 className="text-h3 my-4">{title}</h3>
        <div className="grid grid-cols-1 gap-4 md:grid-cols-2 lg:grid-cols-3 justify-self-center">
          {contacts.map((contact, i) => (
            <div className="w-auto px-4" key={"contact-" + i}>
              <Contact
                {...contact}
                showEmail={false}
                image={contact.image}
                defaultImage={defaultImage}
              />
            </div>
          ))}
        </div>
        <p className="text-h3 my-4 project-team-disclaimer">
          Members of the Project Team are listed for informational purposes; the U.S. Government does not endorse or recommend named organizations or entities.
        </p>
      </section>
    )
  }
  else {
    return (
      <section className="m-responsive"> 
        <h3 className="text-h3 my-4">{title}</h3>
        <div className="grid grid-cols-1 gap-4 md:grid-cols-2 lg:grid-cols-3 justify-self-center">
          {contacts.map((contact, i) => (
            <div className="w-auto px-4" key={"contact-" + i}>
              <Contact
                {...contact}
                showEmail={false}
                image={contact.image}
                defaultImage={defaultImage}
              />
            </div>
          ))}
        </div>
      </section>
    )
  }


}
