import React, { FunctionComponent } from "react"
import { Link } from "gatsby"
import { statusOutput } from "../utils"
import { Contact, MarkdownText, ContactType } from "."

interface ProjectContactProps extends ContactType {
  status: string
  projectInterestLink?: string
  openText: string
  ongoingText: string
  completeText: string
  summaryPDF: string //EP-33: KLW - Adding field for project description pdf file
}

export const MainContact: FunctionComponent<ProjectContactProps> = ({
  status,
  projectInterestLink,
  openText,
  ongoingText,
  completeText,
  summaryPDF, //EP-33: KLW - Adding field for project description pdf file
  ...contactProps
}) => {
  return (
    <>
      <h3 className="hidden">Contact and Project Updates</h3>
      <div className="w-full p-8 mb-8 bg-gray-100">
        <h4 className="text-h4">
          {statusOutput(
            status,
            "This project is open. Interested in collaborating?",
            "The project is in progress.",
            "The project is complete."
          )}
        </h4>
        <div className="text-black text-body">
          <MarkdownText
            text={statusOutput(status, openText, ongoingText, completeText)}
          />
        </div>
        <div className="mt-4">
          {status === "open" ? (
            projectInterestLink ? (
              // <a href={projectInterestLink} target="_blank" rel="noopener">
              //   <button className="btn">Express interest</button>
              // </a>
              // EP-28: KLW - Changing the way the anchor tag and buttons are nested
              <a href={projectInterestLink} target="_blank" rel="noopener"
                className="btn">
                Express Interest
              </a>
            ) : contactProps.email ? (
              // <a href={`mailto:${contactProps.email}`}>
              //   <button className="btn">Email point of contact</button>
              // </a>
              // EP-28: KLW - Changing the way the anchor tag and buttons are nested
              <a href={`mailto:${contactProps.email}`}
                className="btn">
                Email Point of Contact
              </a>
            ) : (
              // <Link to={"/contact"}>
              //   <button className="btn-white">Ask a question</button>
              // </Link>

              // EP-28: KLW - Changing the way the anchor tag and buttons are nested
              <Link to={"/contact"}
                className="btn-white">
                Ask a question
              </Link>
            )
          ) : (
            // <Link to={"/contact"}>
            //   <button className="btn-white">Ask a question</button>
            // </Link>

            // EP-28: KLW - Changing the way the anchor tag and buttons are nested
            <Link to={"/contact"}
              className="btn-white">
              Ask a question
            </Link>
          )}
        </div>
      </div>
      {/* EP-33: KLW - Adding box for downloading PDF*/} 
      <div className="w-full p-8 my-8 bg-gray-100">
        <h4 className="text-h4">Download and share the project summary</h4> 
        <div className="mt-4 text-body">
          <a download href={summaryPDF}
            className="btn">
            Download
          </a>
        </div>
      </div>

      <div className="w-full p-8 my-8 bg-gray-100">
        <h4 className="text-h4">Project point of contact</h4>
        <div className="mt-4 text-body">
          <Contact {...contactProps} showEmail={true} />
        </div>
      </div>
    </>
  )
}
